import React from 'react'
import { NavLink } from 'react-router-dom'
import navLogo from "../../images/nav3.webp"

export default function Navbar(props) {
  let id=6;
    return (//Object.keys(props.loggedUser).length!==0
        <div className={`  fixed-top ` } >
            <nav className={ `  navbar navbar-expand-lg navbar-dark nav`}>
  <div className="container-fluid">
    <a className="navbar-brand" href="#"><img src={navLogo} alt="Movies"  className="rounded-circle"/></a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        
      {(props.loggedUser)?<>
                    <li className=" nav-item "> <NavLink className="nav-link" activeClassName={'activeClass'} to={"/home"} state={{id}} >Home </NavLink>  </li>
                    <li className=" nav-item "> <NavLink className="nav-link" activeClassName={'activeClass'} to="/movies">Movies </NavLink>  </li>
                    <li className=" nav-item "> <NavLink className="nav-link" activeClassName={'activeClass'} to="/tv">TV </NavLink>  </li>
                    <li className=" nav-item "> <NavLink className="nav-link" activeClassName={'activeClass'} to="/persons">Persons </NavLink>  </li>
                </>:""}
      
       
      </ul>
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                   {
                   (!props.loggedUser)?<>
                    <li className="nav-item"> <NavLink className="nav-link" to="/login">Login </NavLink>  </li>
                    <li className="nav-item"> <NavLink className="nav-link" to="/register">Register </NavLink>  </li>
                    </> : <li className="nav-item" onClick={props.logOut}  > Logout  </li>
                    }
                </ul>
    </div>
  </div>
</nav>
        </div>
    )
}
