import React, { useContext } from 'react';
import { MoviesContext } from '../../MoviesContext';
import OwlCourselHome from '../OwlCuroselHome/OwlCourselHome';
import style from './Home.module.css';
import Fade from 'react-reveal/Fade';
import Flip from 'react-reveal/Flip';
import Rotate from 'react-reveal/Rotate';
import { useLocation } from 'react-router-dom';
import { NavLink } from 'react-router-dom'
export default function Home(props) {
 
    let location =useLocation()
    let { trendingMovies , trendingTv, trendingPersons ,imgPrefix} = useContext(MoviesContext);
// console.log(location.state);  
// console.log("location");  
let mediaId="asdas";
    function printTrending(mediaType, header) {
        return (
            <>
             <div className="row my-5">
                    <div className={`col-md-4 d-flex align-items-center bg-dark ${style.media_Header} position-relative my-3 flex-column justify-content-center`}>
                     
                        <h2  className="h1 my-5 bg-danger p-3 text-capitalize"> <p>  Trending <Rotate top left>{(header=='tv')?header+"":header+'s'}</Rotate></p> <p> To Watch </p> <p> Right Now </p></h2>

                        
                    </div>

                    {mediaType ? mediaType.slice(0,10).map((movie, index) => <div key={index} className="col-md-2">
                        <Fade bottom cascade ><NavLink to={`/mediaDetails/${header}/${movie.id}`} className="movie">
                            <img className='w-100' src={(header === 'person') ? imgPrefix + movie.profile_path : imgPrefix + movie.poster_path} alt={movie.title} />
                            <h3 className='h6 p-2 bg-dark text-center'>{(header === "movie") ? movie.title : movie.name}</h3>
                          </NavLink></Fade>
                    </div>) : ''}
                  

               </div>

            </>
            
            )
    }
    return (
        <>
            <div id={style.HomeSliderPage}>

            
                <div className="  h-100 d-flex  align-items-end  ">
                     <OwlCourselHome></OwlCourselHome> 
                </div>
                
            </div>
        <div className="container">

            {printTrending(trendingMovies.slice(0,10) , "movie")}
            {printTrending(trendingTv , "tv")}
            {printTrending(trendingPersons, "person")}

        </div>
           
        </>

    )
}
