import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { MoviesContext } from '../../MoviesContext';
import SimilarMedia from '../SimilarMediaComponent/SimilarMedia';

import style from './MediaDetails.module.css'
export default function MediaDetails() {
    const { getMediaById, getSimilarMedia, imgPrefix } = useContext(MoviesContext)
    let { id, mediaType } = useParams();
    const [media_details, setMedia_details] = useState({});
    let mediaCategories = [];
    useEffect(async () => {
        let x = await getMediaById(id, mediaType);
        setMedia_details(x);
        console.log("hello from Details");
        mediaCategories = mediaType === 'person' ? x.also_known_as : x.genres;
        console.log(mediaCategories);
    }, [id])
    function convertTime(time) {
        return `${Math.floor(time / 60)}h${time % 60}m`;
    }
    function personDetails() {
        return (
            <>
                <div className="col-md-4 mx-auto">
                    <div className="imgWraper position-relative">
                        <img className="w-100" src={imgPrefix + media_details.profile_path} alt={media_details.name} />
                        <div className={`bg-info fs-3 p-2 rounded-2 ${style.timeDiv}`}>{media_details.known_for_department} </div>
                    </div>
                </div>
                <div className="col-md-6 m-auto   ">
                    <div className={`${style.mediaDetails} h-100 p-4`}><h2 className="h1 fw-bolder"> {media_details.name}</h2>
                        <h3 className="text-muted"> Also known AS :{media_details.original_title}</h3>
                        <div className="d-flex justify-content-around flex-wrap py-2">
                            {media_details.also_known_as?.map((gen, index) => <div className={`${style.bg_lniear} py-2 px-3 my-2 rounded-2 fw-bold fs-5`}>{gen}</div>)}
                        </div>
                        <div className="voteDetails my-3 fs-5">
                            {/* <p  > <span className={media_details.adult?"bg_red":"bg-primary py-2 px-3 rounded-2 fs-5" }> For Adult </span></p> */}
                            <p class="py-2">Birthday : {media_details.birthday}</p>
                            <p class="py-2">Place Of Birth : {media_details.place_of_birth}</p>
                            <p class="py-2"> popularity : {media_details.popularity}</p>
                            {media_details.deathday ? <p class="py-2">Death date : {media_details.release_date}</p> : ""}
                        </div>

                        {/* <p className="overView fs-3 mb-auto"><span className="fs-5"> Biography : </span><br /> {media_details.biography}</p> */}
                    </div>


                </div>
            </>
        )
    }
    return (
        <div className="h1 h_10vh">
            <div className="container">
                <div className="row">
                    {mediaType === 'person' ? personDetails() : <>
                        <div className="col-md-4 mx-auto">
                            <div className="imgWraper w-100 position-relative">
                                <img className="w-100" src={imgPrefix + media_details.poster_path} alt={(mediaType === 'tv') ? media_details.name : media_details.title} />
                                <div className={`bg-info fs-3 p-2 rounded-2 ${style.timeDiv}`}>{(mediaType === 'tv') ? media_details.number_of_episodes + "EP" : convertTime(media_details.runtime)} </div>
                            </div>
                        </div>
                        <div className="col-md-6 m-auto">
                            <div className={`${style.mediaDetails} h-100 p-4`}><h2 className="h1 fw-bolder"> {(mediaType === 'movie') ? media_details.title : media_details.name}</h2>
                                <h3 className="text-muted">{(mediaType === 'tv') ? media_details.original_name : media_details.original_title}</h3>
                                <div className="d-flex justify-content-around flex-wrap py-2">
                                    {media_details.genres?.map((gen, index) => <div className={`${style.bg_lniear} py-2 px-3 m-1 rounded-2 fw-bold fs-5`}>{gen.name}</div>)}
                                </div>
                                <div className="voteDetails my-3 fs-5">
                                    {/* <p  > <span className={media_details.adult?"bg_red":"bg-primary py-2 px-3 rounded-2 fs-5" }> For Adult </span></p> */}
                                    <p class="py-2">Vote : {media_details.vote_average}</p>
                                    <p class="py-2">Vote count : {media_details.vote_count}</p>
                                    <p class="py-2"> popularity : {media_details.popularity}</p>
                                    <p class="py-2">{(mediaType === 'tv') ? `First Air Date : ${media_details.first_air_date}` : `Release date : ${media_details.release_date}`} </p>
                                </div>

                            </div>


                        </div></>}
                </div>
                <div className="row">
                    <p className="overView fs-3 my-3"><span className="fs-5"> {(mediaType === 'person') ? 'Biography :\n' : 'overview :\n'} </span><br />
                        {(mediaType === 'person') ? media_details.biography : media_details.overview}</p>

                </div>
                {/* {get Movie Recommendation} api Url (cahnge mediaType and Id) { https://api.themoviedb.org/3/movie/831405/recommendations?api_key=88aa63dadeb31196c2c990505fbc33db&language=en-US } */}
            </div>
            <SimilarMedia imgPrefix={imgPrefix} similarMedia={ getSimilarMedia} mediaType={mediaType} id={id} ></SimilarMedia>
        </div>////donot forget to handle the part of changing the movie id in the url to get another movie details but it doesnot work as the url doesnot change as you are in the same component 
    )
}// similarMedia={() => getSimilarMedia(id, mediaType)}
