import axios from 'axios';
import React, { createContext, useEffect, useState } from 'react'

export let MoviesContext = createContext([]);
export default function MoviesContextProvider(props) {
    let api_key = '88aa63dadeb31196c2c990505fbc33db';
    const [trendingMovies, setTrendingMovies] = useState([]);
    const [trendingTv, setTrendingTv] = useState([]);
    const [trendingPersons, setTrendingPersons] = useState([]);
    const [searchMoviesArray, setSearchMoviesArray] = useState([]);
    let imgPrefix = 'https://image.tmdb.org/t/p/w500';


    async function getTrendingMedia(trendingMedia, setTrendingMedia) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${trendingMedia}/week?api_key=${api_key}`)
        setTrendingMedia(data.results);
        console.log(data);
    }

    async function getMediaById(media_id, media_type) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/${media_type}/${media_id}?api_key=${api_key}&language=en-US`)
        console.log(data);
        return data;
    }

    async function searchMovies(searchQuery) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${api_key}&language=en-US&query=${searchQuery}&page=1&include_adult=false`);
        setSearchMoviesArray(data.results);
        console.log(data);
        return data.results;
    }

    async function getMovies(category, mediaType = 'movie') {
        if (category === 'trending') {
            return mediaType === 'movie' ? trendingMovies : trendingTv;
        }
        let moviesByCategoryUrl = `https://api.themoviedb.org/3/${mediaType}/${category}?api_key=${api_key}&language=en-US`;
        const { data } = await axios.get(moviesByCategoryUrl);
        return data.results;
    }
///https://api.themoviedb.org/3/movie/{movie_id}/similar?api_key=88aa63dadeb31196c2c990505fbc33db&language=en-US&page=1
//data.results.length()>6?data.results.slice(0,6):
async function getSimilarMedia(mediaId, mediaType = 'movie') {
    
    let moviesByCategoryUrl = `https://api.themoviedb.org/3/${mediaType}/${mediaId}/similar?api_key=${api_key}&language=en-US`;
    const { data } = await axios.get(moviesByCategoryUrl);
    return data.results;
}
    useEffect(() => { // component did mount
        getTrendingMedia('movie', setTrendingMovies);
        getTrendingMedia('tv', setTrendingTv);
        getTrendingMedia('person', setTrendingPersons);
    }, [])

    return (
        <MoviesContext.Provider value={{ trendingMovies, trendingTv, trendingPersons, getMediaById, searchMovies, getMovies,getSimilarMedia, imgPrefix }}>
            {props.children}
        </MoviesContext.Provider>
    )
}
