import React, { useContext } from 'react'
import OwlCarousel from 'react-owl-carousel';  
import 'owl.carousel/dist/assets/owl.carousel.css';  
import 'owl.carousel/dist/assets/owl.theme.default.css'; 
import { MoviesContext } from '../../MoviesContext';

 
export default function OwlCourselHome() {
    let { trendingMovies  ,imgPrefix} = useContext(MoviesContext);
    const options = {
     
        responsiveClass: true,
         
        
    
        smartSpeed: 500,
        responsive: {
            0: {
                items: 1,
            },
            400: {
                items: 1,
            },
            600: {
                items: 2,
            },
            700: {
                items: 3,
            },
            1000: {
                items: 6,
    
            }
        },
    };
    return (
       
    <OwlCarousel {...options } className='owl-theme px-5'   loop  items={6} animateOut={true}  margin={10} autoplay nav>
           
    {
        trendingMovies.map( (movie,index)=>
        <div key={index} className='item'>
        <img src={imgPrefix+movie.poster_path} className="w-100 " alt="" />
        
    </div> 
    )
    }
    
   
    {/* <div class='item'>
        <h4>5</h4>
    </div>
    <div class='item'>
        <h4>6</h4>
    </div>
    <div class='item'>
        <h4>7</h4>
    </div>
    <div class='item'>
        <h4>8</h4>
    </div>
    <div class='item'>
        <h4>9</h4>
    </div>
    <div class='item'>
        <h4>10</h4>
    </div>
    <div class='item'>
        <h4>11</h4>
    </div>
    <div class='item'>
        <h4>12</h4>
    </div> */}
</OwlCarousel>
    )
}
