import jwtDecode from 'jwt-decode';
import { useEffect, useState } from 'react';
import { Switch,Route, Redirect, useHistory } from 'react-router';
import MoviesContextProvider from '../../MoviesContext';
import GuardedRoute from '../Guarded/GuardedRoute';
import Navbar from '../Navbar/Navbar';
import Home from '../Home/Home';
import Login from '../Login/Login';
import MediaDetails from '../MediaDetails/MediaDetails.jsx';
import Movies from '../Movies/Movies';
import Tv from '../Tv/Tv'
import ProtectedRoute from '../ProtectedRouteComponent/ProtectedRoute';
import Register from '../Register/Register';
import Browse from '../Browse/Browse';
import './App.css';
function App() {  
  const [loggedUser, setLoggedUser] = useState(null);
  let history= useHistory();

  function getUser(){
  let userToken= localStorage.getItem("user");
  let userInfo=jwtDecode(userToken);
  setLoggedUser(userInfo);
  // console.log(userInfo);
  // console.log(loggedUser);
}



function logOut(){
  localStorage.removeItem("user");
  setLoggedUser(null);
  history.push("/login");
}

useEffect(() => {
  if(localStorage.getItem('user')){ 
    getUser();// to retrive the user data if the website reloaded
    console.log("inLoggedUser1");
  }
  console.log("inLoggedUser2");
  console.log(loggedUser);
}, [])
  return (
    <div className="App">   
     <Navbar logOut={logOut} loggedUser={loggedUser} />
     
     <Switch>

     {/* <GuardedRoute path='/home' component={Home} auth={loggedUser}  />
     <GuardedRoute path='/movies' component={Movies} auth={loggedUser} /> */}
     <ProtectedRoute path="/movies" component={Movies} context={MoviesContextProvider} />
     
     <ProtectedRoute path="/tv" component={Tv} context={MoviesContextProvider} />
     {/* <ProtectedRoute path="/browse" component={Browse} context={MoviesContextProvider} /> */}

     <ProtectedRoute path="/mediaDetails/:mediaType/:id" component={MediaDetails} context={MoviesContextProvider} />
    <ProtectedRoute path="/home" component={Home} context={MoviesContextProvider} />
      
       {/* <Route path="/home"> <Home/> </Route> 
       <Route path="/movies"> <Movies/> </Route> */}
       <Route path="/login"  render={(props) => <Login {...props} getUser={getUser}/> } />
       <Route path="/register" render={(props)=> <Register {...props}/>} /> 
      <Route path="/" render={()=> <Redirect to="/home"/>} /> 
       </Switch> 
 
    </div>
  );
}

export default App;
