import React, { useState } from 'react'
import axios from 'axios'
import Joi from 'joi';
import style from './../Login/login.module.css'
export default function Register(props) {
    let [user, setUser] = useState({ first_name: "", last_name: "", email: "", age: 0, password: "" });
    let [error, setError] = useState(null);
    let [validationError, setValidationError] = useState([]);
    const [loading, setLoading] = useState(false);
    function getUser(e) {
        setUser({ ...user, [e.target.name]: e.target.value });
        //     let tempUser= {...user};
        //     tempUser[e.target.name]=e.target.value;
        //       setUser(tempUser);
        // console.log("hello");
        // console.log(user);
    }

    function formValidation() {
        let schema = Joi.object({ // schema is best practice Name not a must .
            first_name: Joi.string().min(3).max(10).required(),
            last_name: Joi.string().min(3).max(10).required(),
            email: Joi.string().email({ tlds: { allow: ['com', 'net'] } }).required(),
            age: Joi.number().required().min(7).max(80),
            password: Joi.string().required().pattern(new RegExp('^[a-zA-Z0-9]{3,20}$'))
        })
        return schema.validate(user, { abortEarly: false });
    }

    function loginNow() {
        props.history.push('/login');
    }
    async function submitRegisterForm(e) {
        e.preventDefault();
        setError(null); 
        let formValidationResponse = formValidation();
        if (formValidationResponse.error) {
            console.log(formValidationResponse.error.details);
            setValidationError(formValidationResponse.error.details);
            
        }
        else {
            setValidationError([]);// to remove the old errors from the screen
            setLoading(true);
            let { data } = await axios.post('https://route-egypt-api.herokuapp.com/signup', user);
            if (data.message === "success") {
                loginNow();

            }
            else {
                console.log("whattt");

                console.log(data.errors);
                setError(data.message);
                console.log("error");
                console.log(error);
            }

        }
        //

        setLoading(false);
        // console.log("hello");
        // console.log(user );

    }
console.log(validationError);

    return (
        <div className="container   d-flex justify-content-center align-items-center vh-100">

            <div className={`h_10vh w-75 mt-5 py-5`}>
                <h1 className="text-center">Register Now</h1>
                <form onSubmit={submitRegisterForm}>
                    <div className="my-2">
                        <label htmlFor="">First Name :</label>
                        <input className="form-control my-2" type="text" name="first_name" onChange={getUser} />
                        {/* {validationError && validationError[0].context.key==='"first_name"'&& <div className="alert alert-danger" > {validationError}</div> } */}
                        {/* {
                        (validationError.filter((err) => err.context.key=="first_name")&&
                        <div className="alert alert-danger my-2 fs-5" >
                            { validationError.filter((err) => err.context.key=="first_name")[0]?.message}
                        </div>
                        )} */}

                    </div>
                    <div className="my-2">
                        <label htmlFor="">Last Name :</label>
                        <input className="form-control my-2" type="text" name="last_name" onChange={getUser} />
                        {/* {
                        validationError.filter((err) => err.context.key==="last_name")&&
                        (
                        <div className="alert alert-danger my-2 fs-5" >
                            { validationError.filter((err) => err.context.key=="last_name")[0]?.message}
                        </div>
                        )}           */}         </div> 
                    <div className="my-2">
                        <label htmlFor="">E-mail :</label>
                        <input className="form-control my-2" type="email" name="email" onChange={getUser} />
                        {
                        error&&
                        (
                        <div className="alert alert-danger my-2 fs-5" >
                            email alerady exist 
                        </div>
                        )}     
                                       </div>
                    <div className="my-2">
                        <label htmlFor="">Age :</label>
                        <input className="form-control my-2" type="number" name="age" onChange={getUser} />
                        {/* {
                        validationError.filter((err) => err.context.key=="age")&&
                        (
                        <div className="alert alert-danger my-2 fs-5" >
                            { validationError.filter((err) => err.context.key=="age")[0]?.message}
                        </div>
                        )}                   */}
                          </div>
                    <div className="my-2">
                        <label htmlFor="">Password :</label>
                        <input className="form-control my-2" type="password" name="password" onChange={getUser} />
                        {/* {
                        validationError.filter((err) => err.context.key=="password")&&
                        (
                        <div className="alert alert-danger my-2 fs-5" >
 {
                        validationError.map((err, index) => <div key={index} className="alert alert-danger my-2 fs-5" > {
                        err.path == 'password' && err.type != "string.empty" ? "invalid password" : err.message}</div>)}
                            {/* { validationError.filter((err) => err.context.key=="password")[0].message} */}
                        {/*</div>
                        )}                     */}
                        
                        </div>
                    {
                        validationError.map((err, index) => <div key={index} className="alert alert-danger my-2 fs-5" > {err.path == 'password' && err.type != "string.empty" ? "invalid password" : err.message}</div>)}
                    {/* the above condation in the alert div insure that : if(error in password) then if (password is empty) print (apiMessage) else print( invalidMessage )*/}
                    <div className="text-center ">
                        <p className=" mt-4">Have an account ? <span onClick={loginNow} className={`${style.signUp}`} > signin </span> now </p>
                        <button className="btn btn-outline-info mt-3  w-25"> <h3> {loading ? <i className="fas fa-spinner fa-spin "></i> : "register"}  </h3> </button>
                    </div>
                </form>
            </div>
        </div>
    )
}
