import React, { useEffect } from 'react'
import Fade from 'react-reveal/Fade';
import { NavLink } from 'react-router-dom'
import { useState } from 'react/cjs/react.development';
import { MoviesContext } from '../../MoviesContext';
import style from "./../Tv/Tv.module.css"
export default function SimilarMedia(props) {
  let  {similarMedia,imgPrefix ,mediaType,id } = props;
  const [similarMediaList, setSimilarMediaList] = useState([]);
  useEffect(async() => {
      let x= await similarMedia(id,mediaType);
    //  let x= await similarMedia();
     setSimilarMediaList(x);
     console.log(x); 
  }, [id])
  
  
//   console.log(similarMedia);
  
    return (
        <div className={`container ${style.bg_lniear} pt-3 `}>
            <h2 className="text-center h1">Similar { mediaType==='tv'?'Tv Series':'Movies'} </h2>
                <div className="row ">
            { similarMediaList.map((movie, index) =>   <div className="col-md-3" key={index}><Fade bottom cascade> 
            <NavLink to={`/mediaDetails/${mediaType}/${movie.id}`} className="movie">
           <div className="position-relative">
                <img className='w-100' src={imgPrefix + movie.poster_path} alt={movie.title} />
           <div className={`${style.vote} rounded-circle position-absolute  bg-dark d-flex justify-content-center align-items-center `}> {movie.vote_average} </div> </div> 

          <h3 className='h6 p-2 bg-dark text-center'> { mediaType==='tv'? movie.name: movie.original_title}</h3>
        </NavLink></Fade> </div>   )}
      

        </div>
        </div>
    )
}
