import React ,{useState} from 'react'
import style from './login.module.css'
import Joi from 'joi';
import axios from 'axios';

export default function Login(props) {
    const [loading, setLoading] = useState(false);
    const [user, setUser] = useState({email:"",password:""});
    let [error, setError] = useState(null);
    let [validationError, setValidationError] = useState([]);
  

    function validateLoginForm(){
        let schema =Joi.object({
            email:Joi.string().email({tlds :{allow : ['net',"com" ,"eg"]}  }).required(),
            password:Joi.string().required().pattern(new RegExp('^[a-zA-Z0-9]{3,20}$'))
        })
        return schema.validate(user ,{abortEarly:false});
    }
    async function submitRegisterForm(e){
        e.preventDefault();
        setLoading(true);
        let validationLoginResponse=validateLoginForm();
        if(validationLoginResponse.error){
            console.log(validationLoginResponse.error.details);
            setValidationError(validationLoginResponse.error.details);
            setLoading(false);
        }
        else{
            let { data }= await axios.post('https://route-egypt-api.herokuapp.com/signin', user);
            let{message , token}=data;
            if (message === "success") {
                localStorage.setItem("user",data.token);
                props.getUser();
                setLoading(false);
                props.history.push('/home');
            }
            else {
                setLoading(false);
                setError(message);
            }
        }
        
        console.log("hello2");
        console.log(user);
 
    }

    function getUser(e){
setUser({...user , [e.target.name]:e.target.value});
    }
    function registerNow(){
        props.history.push('/register');
    }
    return (
        <div className="container   d-flex justify-content-center align-items-center vh-100">

            <div className={`h_10vh w-75 mt-5 py-5`}>
                <h1 className="text-center">Login Now</h1>
                <form onSubmit={submitRegisterForm}>
                    <div className="my-2">
                        <label htmlFor="email">Email :</label>
                        <input type="email" id="email" className="form-control my-2" name="email" onChange={getUser} />
                    </div>
                    <div className="my-2">
                        <label htmlFor="password">password :</label>
                        <input type="password" id="password" className="form-control my-2" name="password" onChange={getUser} />
                         {error && <div className="alert alert-danger" >{error}</div> }
                    </div>
                    <div className=" text-center ">
                    { validationError.map((err,index) =>  <div key={index} className="alert alert-danger my-2 fs-5" > {err.path=='password'&&err.type!="string.empty"?"invalid password":err.message }</div>) }
                    <p className=" mt-4">If you don't have an account, <span onClick={registerNow} className={`${style.signUp}`} > signup </span>now for free </p>
                        <button className="btn btn-outline-info mx-auto mt-3   w-25"> <h3> {loading ? <i 
                        className="fas fa-spinner fa-spin "></i> : "login"}  </h3> </button>
                    
                    </div>
                </form>
            </div>
        </div>
    )
}
